<template>
  <div class="app-main">
   
    <div class="search-body margin-t-sm">
      <div class="fl margin-t-sm margin-r-sm">
        <span>用户名：</span>
        <el-input v-model="username" clearable class="input-w" placeholder="请输入" />
      </div>
       <div class="margin-t">
        <el-button type="primary" :loading="loadinghandleSearch" @click="handleSearch"> 查询 </el-button>
        <el-button @click="reset"> 重置 </el-button>
        <el-button type="primary" @click="operatTableAdd"> 新增用户 </el-button>
        <el-button type="primary" :disabled="multipleSelection.length !== 1" @click="operatTableEdit"> 修改 </el-button>
        <el-button type="danger" :disabled="multipleSelection.length === 0" @click="deleteData"> 删除 </el-button>
      </div>
    </div>
    <div class="margin-t-lg">
      <el-table
        ref="Table"
        :data="tableData"
        stripe
        border
        height="550"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55"
        />
        <el-table-column type="index" width="65" align="center" label="序号" :index="indexMethod" />

        <el-table-column
          prop="username"
          label="用户名称"
          align="center"
          width="110"
        />
        <el-table-column
          prop="userid"
          label="用户id"
          align="center"
          min-width="200"
        />
        <el-table-column
          prop="audio_clone_times"
          align="center"
          label="声音克隆次数"
          min-width="200"
        />
        <el-table-column
          prop="credit"
          align="center"
          label="积分余额"
          width="120"
        />
        <el-table-column
          prop="create_time"
          align="center"
          label="创建时间"
          width="120"
        />
        <el-table-column
          prop="modify_user"
          align="center"
          label="修改人"
          width="120"
        />
        <el-table-column
          prop="modify_time"
          align="center"
          label="修改时间"
          width="120"
        />
      </el-table>
      <div class="text-right margin-t-sm">
        <el-pagination
          background
          :page-sizes="pageSizes"
          :current-page="inquire.pageNum"
          :page-size="inquire.pageSize"
          :layout="pageLayout"
          :total="tabletotal"
          @size-change="pageSizeChange"
          @current-change="pageCurrentChange"
        />
      </div>
    </div>
    <el-dialog
      title="新增用户"
      :visible.sync="dialogFormAdd"
      width="30%"
    >
      <el-form label-width="120px" :model="addData">
        <el-form-item label="用户ID">
          <el-input v-model="addData.userid" />
        </el-form-item>
        <el-form-item label="用户名称">
          <el-input v-model="addData.username" />
        </el-form-item>
        <el-form-item label="用户密码">
          <el-input v-model="addData.password" />
        </el-form-item>
        <el-form-item label="声音克隆次数">
          <el-input v-model="addData.audio_clone_times" type="number" />
        </el-form-item>
        <el-form-item label="积分余额">
          <el-input v-model="addData.credit" type="number" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormAdd = false">取 消</el-button>
        <el-button type="primary" @click="submitAddUser">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="用户修改"
      :visible.sync="dialogFormEdit"
      width="30%"
    >
      <el-form label-width="120px" :model="editData">
        <el-form-item label="用户ID">
          <el-input v-model="editData.userid" disabled />
        </el-form-item>
        <el-form-item label="用户名称">
          <el-input v-model="editData.username" />
        </el-form-item>
        <el-form-item label="声音克隆次数">
          <el-input v-model="editData.audio_clone_times" type="number" />
        </el-form-item>
        <el-form-item label="积分余额">
          <el-input v-model="editData.credit" type="number" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEdit = false">取 消</el-button>
        <el-button type="primary" @click="submitEditUser">确 定</el-button>
      </span>
    </el-dialog></div>
</template>

<script>
import { getUser, deleteUser, editUser, addUser } from '@/api/baseinfo/userControl.js'

import page from '@/mixins/page'

export default {
  name: 'UserControl',
  mixins: [page],
  data() {
    return {
      imgUrl: process.env.VUE_APP_BASE_API + '/index/ucenter/upload_img',
      site_code: '',
      username: '',
      multipleSelection: [],
      time_type: '1',
      is_enable: 1,
      start_time: '',
      end_time: '',
      tabletotal: 0,
      options: [{
        value: 1,
        label: '是'
      }, {
        value: 0,
        label: '否'
      }],
      tableData: [],
      dialogFormAdd: false,
      dialogFormEdit: false,
      form: {
        site_code: '',
        site_name: '',
        item: [],
        start_time: '',
        end_time: '',
        is_enable: '1',
        remark: ''
      },
      formType: '',
      rules: {
        site_name: [
          { required: true, message: '请输入网点名称', trigger: 'blur' }
        ],
        site_code: [
          { required: true, message: '请输入网点编码', trigger: 'blur' }
        ],
        is_enable: [
          { required: true, message: '请选择启用状态', trigger: 'change' }
        ],
        item: [
          { required: true, message: '服务项目必选', trigger: 'change' }
        ],
        start_time: [
          { required: true, message: '请选择开始履行合同时间', trigger: 'change' }
        ],
        end_time: [
          { required: true, message: '请选择结束履行合同时间', trigger: 'change' }
        ],
        remark: [
          { required: false, message: '', trigger: 'blur' }
        ]
      },
      rolelist: [],
      formLabelWidth: '140px',
      exportUrl: '',
      fileList: [],
      centerDialogVisible: false,
      imgShowUrl: [],
      secoptions: [],
      secLoading: false,
      editData: {},
      addData: {
        userid: '',
        username: '',
        password: '',
        audio_clone_times: '',
        credit: ''
      },
      loadinghandleSearch: false,
      loadingsubmit: false
    }
  },
  created() {
    this.search()
  },
  methods: {
    submitEditUser() {
      if (!this.editData.username || this.editData.audio_clone_times === '' || this.editData.audio_clone_times < 0 || this.editData.credit === '' || this.editData.credit < 0) {
        this.$message.error('请填写完整信息')
        return
      }
      const param = new FormData()
      param.append('userid', this.editData.userid)
      param.append('username', this.editData.username)
      param.append('audio_clone_times', this.editData.audio_clone_times)
      param.append('credit', this.editData.credit)
      editUser(param).then(res => {
        if (res.code === 200) {
          this.$message.success('修改成功')
          this.dialogFormEdit = false
          this.search()
        } else {
          this.$message.error(res.data.message)
        }
      })
    },
    submitAddUser() {
      if (!this.addData.password || !this.addData.userid || !this.addData.username || this.addData.audio_clone_times === '' || this.addData.audio_clone_times < 0 || this.addData.credit === '' || this.addData.credit < 0) {
        this.$message.error('请填写完整信息')
        return
      }
      const param = new FormData()
      param.append('userid', this.addData.userid)
      param.append('username', this.addData.username)
      param.append('password', this.addData.password)
      param.append('audio_clone_times', this.addData.audio_clone_times)
      param.append('credit', this.addData.credit)
      addUser(param).then(res => {
        if (res.code === 200) {
          this.$message.success('新增成功')
          this.dialogFormAdd = false
          this.search()
        } else {
          this.$message.error(res.data.message)
        }
      })
    },
    handleSearch() {
      this.inquire.pageNum = 1
      this.search()
    },
    async search() {
      this.loadinghandleSearch = true
      const data = new FormData()
      data.append('pageSize', this.inquire.pageSize)
      data.append('page', this.inquire.pageNum)
      const res = await getUser(data)
      this.loadinghandleSearch = false
      this.tableData = res.data.rows || []
      this.tabletotal = res.data.total
    },
    reset() {
      this.site_code = ''
      this.time_type = '1'
      this.start_time = ''
      this.end_time = ''
      this.is_enable = 1
    },

    close() {
      this.loadingsubmit = false
      this.dialogFormAdd = false
      this.$refs.form.resetFields()
    },
    operatTableEdit() {
      if (this.multipleSelection.length !== 1
      ) {
        this.$message.error('请选择一条数据进行编辑!')
        return
      }
      this.editData = this.multipleSelection[0]
      this.dialogFormEdit = true
    },
    operatTableAdd() {
      this.dialogFormAdd = true
    },
    deleteData() {
      if (this.multipleSelection.length === 0) {
        this.$message.error('请选择要删除的数据!')
        return
      }
      this.$confirm('此操作将永久删除选中的' + this.multipleSelection.length + '条数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const arr = this.multipleSelection.map((item) => {
          return item.id
        })
        const data = new FormData()
        data.append('ids', arr)
        deleteUser(data).then(res => {
          if (res.code === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.search()
          } else {
            this.$message.error(res.msg)
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    }
  }
}
</script>

<style lang="scss" scoped>
.margin-t{
  margin-left: 100px;
}
.search-body{
  display: flex;
  flex-wrap: wrap;
  >*{
    display: inline-block;
  }
  .search-item:nth-child(1){
    width:320px ;
    >*{
      display: inline-block;
      padding-bottom: 8px;
    }
    span{
      width: 100px;
      font-size: 14px;
    }
  }
  .search-item:nth-child(2){
    width:350px ;
    >*{
      display: inline-block;
    }
    span{
      width: 130px;
      font-size: 14px;
    }
  }
  .search-item:nth-child(3){
    width:160px ;
    >*{
      display: inline-block;
    }
    span{
      width: 76px;
      font-size: 14px;
    }
  }
}
.input-w{display: inline-block;width: 200px;}
.show_img{
  width: 100%;
}
.dialog-footer-c{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.app-main{
  width:80%;
  margin: 30px auto;
}
</style>



