import request from '../index.js'

export function getUser(data) {
  return request({
    url: '/sysApi/getUser',
    method: 'post',
    data
  })
}

export function deleteUser(data) {
  return request({
    url: '/sysApi/deleteUser',
    method: 'post',
    data
  })
}

export function editUser(data) {
  return request({
    url: '/sysApi/set_userinfo',
    method: 'post',
    data
  })
}

export function addUser(data) {
  return request({
    url: '/sysApi/addUser',
    method: 'post',
    data
  })
}



